@media print {
    .questions-block .table-container {
        page-break-before: avoid;
        break-before: avoid-page;
    }
}

.questions-block h2 {
    font-family: Tahoma;
    font-size: 20px;
    margin-block-end: 0.5em;
    page-break-after: avoid;
    break-after: avoid-page;
}

.questions-block .instructions {
    font-family: Tahoma;
    font-size: 18px;
    page-break-after: avoid;
    break-after: avoid-page;
}

.questions-block .table-container {
    width: 100%;
    border-collapse: collapse;
}

.questions-block table {
    width: 100%;
    border-collapse: collapse;
}

.questions-block table tr {
    page-break-inside: avoid;
}

.questions-block table td {
    border: #888 solid 1px;
    padding: 0px;
    position: relative;
    height: 1px;
}

.questions-block .question-number {
    position: absolute;
    float: left;
    width: 25px;
    padding: 4px;
    font-family: Tahoma;
    font-size: 10px;
    color: #888;
}

.answers-block h2 {
    font-family: Tahoma;
    font-size: 16px;
}

.answers-block table {
    width: 100%;
    border-collapse: collapse;
}

.answers-block table td {
    border: none;
    padding: 0px;
    position: relative;
}

.answers-block .answer-number {
    float: left;
    width: 15px;
    padding: 2px;
    font-family: Tahoma;
    font-size: 6px;
    color: #888;
}
