.question-oneliner {
    box-sizing: border-box;
    position: relative;
    padding: 10px 10px 10px 20px;
    width: 40%;
    border-right: dotted #888 1px;
}

.question-oneliner .answer {
    position: absolute;
    left: 100%;
    top: 0;
    padding: 4px;
    font-family: Tahoma;
    font-size: 10px;
    color: #888;
}