.question-long-twoliner {
    padding: 0;
    width: 100%;
    height: 100%;
}

.question-long-twoliner .question {
    padding: 10px 10px 50px 20px;
}

.question-long-twoliner .workings {
    position: absolute;
    bottom: 0;
    box-sizing: border-box;
    height: 40px;
    width: 100%;
    padding: 4px;
    font-family: Tahoma;
    font-size: 10px;
    color: #888;
    border-top: dotted #888 1px;
}

.question-long-twoliner .answer {
    position: absolute;
    bottom: 0;
    left: 50%;
    box-sizing: border-box;
    width: 50%;
    height: 40px;
    padding: 4px;
    font-family: Tahoma;
    font-size: 10px;
    color: #888;
    border-left: dotted #888 1px;
}