.question-twoliner {
    padding: 0;
    width: 100%;
    height: 100%;
}

.question-twoliner .question {
    box-sizing: border-box;
    padding: 10px 10px 50px 20px;
    width: 35%;
}

.question-twoliner .workings {
    position: absolute;
    left: 40%;
    top: 0;
    box-sizing: border-box;
    height: 100%;
    border-left: dotted #888 1px;
    padding: 4px;
    font-family: Tahoma;
    font-size: 10px;
    color: #888;
}
.question-twoliner .answer {
    position: absolute;
    bottom: 0;
    box-sizing: border-box;
    height: 40px;
    width: 40%;
    padding: 4px;
    font-family: Tahoma;
    font-size: 10px;
    color: #888;
    border-top: dotted #888 1px;
}