table.fraction {
    width: auto;
}

table.fraction td.numerator {
    padding: 0 4px;
    border: solid black;
    border-width: 0 0 1px 0;
    text-align: center;
}

table.fraction td.denominator {
    padding: 0 4px;
    border: none;
    text-align: center;
}

table.fraction td.whole {
    padding: 0 2px 0 0;
    vertical-align: middle;
    border: none;
}

table.fraction td.operation {
    padding: 0 6px;
    vertical-align: middle;
    border: none;
}
