.question-with-workings {
    padding: 0;
    width: 100%;
}

.question-with-workings .question {
    box-sizing: border-box;
    padding: 10px 10px 10px 20px;
    width: 100%;
    border-bottom: dotted #888 1px;
}

.question-with-workings .answer {
    box-sizing: border-box;
    height: 40px;
    width: 100%;
    border-bottom: dotted #888 1px;
    padding: 4px;
    font-family: Tahoma;
    font-size: 10px;
    color: #888;
}

.question-with-workings .workings {
    box-sizing: border-box;
    height: 130px;
    width: 100%;
    padding: 4px;
    font-family: Tahoma;
    font-size: 10px;
    color: #888;
}